export function uniqueArray (arr, key) {
  const obj = {};
  arr = arr.reduce(function (item, next) {
    // eslint-disable-next-line no-unused-expressions
    obj[next[key]] = obj[next[key]] ? '' : item.push(next);
    // obj[next[key]] ? '' : obj[next[key]] = item.push(next);
    return item;
  }, []);
  return arr;
}

/**
 *
 */
function isPlainObject (obj) {
  return typeof obj === 'object' && Object.prototype.toString.call(obj) === '[object Object]';
}

/**
 *
 */
export function deepAssign () {
  const len = arguments.length;
  let target = arguments[0];
  if (!isPlainObject(target)) {
    target = {};
  }
  for (let i = 1; i < len; i++) {
    const source = arguments[i];
    if (isPlainObject(source)) {
      for (const s in source) {
        if (s === '__proto__' || target === source[s]) {
          continue;
        }
        if (isPlainObject(source[s])) {
          target[s] = deepAssign(target[s], source[s]);
        } else {
          target[s] = source[s];
        }
      }
    }
  }
  return target;
}

/**
 * @Description 
 * @author qianyinggenian
 * @date 2023/10/11 011
 */
export function sortUpDate (a, b) {
  return Date.parse(a.creatDate) - Date.parse(b.creatDate);
}

/**
 * @Description 
 * @author qianyinggenian
 * @date 2023/10/11 011
 */
export function sortDownDate (a, b, sortValue = 'creatDate') {
  return Date.parse(b[sortValue]) - Date.parse(a[sortValue]);
}

/**
 * @Description 
 * @author qianyinggenian
 * @date 2024/3/14
 */
export function download (blob, filename = '.xlsx') {
  const url = window.URL.createObjectURL(blob);
  const a = document.createElement('a');
  a.href = url;
  a.download = filename;
  document.body.appendChild(a);
  a.style.display = 'none';
  a.click();
  document.body.removeChild(a);
}
/**
 * @Description txt
 * @author qianyinggenian
 * @date 2024/3/14
 */
export function downloadFile (resultValue, filename = '', filetype = 'txt') {
  if (resultValue) {
    const blob = new Blob([resultValue], { type: 'text/plain' });
    const downloadElement = document.createElement('a');
    const href = window.URL.createObjectURL(blob); // 
    downloadElement.href = href;
    downloadElement.download = `${filename}.${filetype}`; // 
    document.body.appendChild(downloadElement);
    downloadElement.click(); // 
    document.body.removeChild(downloadElement); // 
    window.URL.revokeObjectURL(href); // blob
  } else {
    this.$message.error('');
  }
}
/**
 * @Description 
 * @author qianyinggenian
 * @date 2024/01/09
 */
export function generateRandomNumbers () {
  let d = new Date().getTime();
  const uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, c => {
    const r = (d + Math.random() * 16) % 16 | 0;
    d = Math.floor(d / 16);
    return (c === 'x' ? r : (r & 0x3 | 0x8)).toString(16);
  });
  return uuid;
}
